<template>
    <div class="bindPhone">
        <div v-if="data.isset === 0" class="phone-form">
            <p class="title">{{$L('绑定手机后，您可以使用手机号快捷登录')}}</p>
            <div class="phone-box">
                <el-input size="large"  v-model="newNumber" :placeholder="$L('输入手机号')"></el-input>
                <el-input size="large"  v-model="message" :placeholder="$L('输入手机验证码')">
                    <el-button class="getMess" slot="append" @click="getPhoneMessage">{{$L(' 获取验证码')}}</el-button>
                </el-input>
                <el-button type="primary" @click="bindFunc" class="phone-submit">{{ $L(' 绑定') }}</el-button>
            </div>
        </div>

        <div v-else-if="data.isset === 1" class="phone-form">
            <div v-if="pass === 1">
                <p class="title">{{$L('当前手机号')}}：{{data.oldNumber}}</p>
                <p class="remind">{{$L('更换手机后，下次登录可使用新手机号登录')}}</p>
                <div class="phone-box">
                    <el-input size="large"  :placeholder="$L('输入手机号')"></el-input>
                    <el-input size="large"  :placeholder="$L('输入手机验证码')">
                        <el-button size="large" class="getMess" @click="getNewPhoneMessage" slot="append">{{$L(' 获取验证码')}}</el-button>
                    </el-input>
                    <el-button type="primary" @click="modifyFunc" class="phone-submit">{{ $L('提交') }}</el-button>
                </div>
            </div>
            <div class="safetyCheck" v-else-if="pass === 0">
                <el-form class="phone-form">
                    <el-tabs v-model="activeName">
                        <el-tab-pane :label="$L('密码验证')" name="pwd">
                            <div class="phone-box">
                                <el-input size="large" :placeholder="$L('输入登录密码')"></el-input>
                                <el-button type="primary" @click="pwdCheck" class="phone-verify">{{ $L('验证') }}</el-button>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane :label="$L('手机验证')" name="phone">
                            <p class="title">{{$L('当前手机号')}}：{{ data.mobileNum }}</p>
                            <div class="phone-box">
                                <el-input size="large" v-model="message" :placeholder="$L('输入手机验证码')">
                                    <el-button class="getMess" slot="append" @click="getPhoneMessage">{{$L(' 获取验证码')}}</el-button>
                                </el-input>
                                <el-button type="primary" @click="phoneCheck" class="phone-verify">{{ $L('验证') }}</el-button>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "bindPhone",
        data() {
            return {
                isBind: 1,
                pass: 0,
                newNumber: "",
                message: "",
                activeName: "pwd",
                type: "phone",
                isPwd: 0,
                data : this.$route.params
            };
        },
        methods: {
            getPhoneMessage() {
                this.$message({
                    message: this.$L("手机验证码发送成功咯！"),
                    type: "success"
                });
            },
            getNewPhoneMessage() {
                this.$message({
                    message: this.$L("新手机验证码发送成功咯！"),
                    type: "success"
                });
            },
            bindFunc() {
                this.$message({
                    message: this.$L("手机号绑定成功咯！"),
                    type: "success"
                });
                this.$emit("closeDialog");
            },
            modifyFunc() {
                this.$message({
                    message: this.$L("手机号修改成功咯！)"),
                    type: "success"
                });
                this.$emit("closeDialog");
            },
            pwdCheck() {
                this.pass = 1;
            },
            phoneCheck() {
                this.pass = 1;
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib";
    .bindPhone {
        position: absolute;
        top: 0;
        left: 0;
        height: 400px;
        background-color: #fff;
        padding: 20px;
        .phone-form {
            .title {
                text-align: center;
                font-size: 16px;
                padding-top: 30px;
            }
            .remind {
                text-align: center;
                font-size: 12px;
                color: #999999;
                margin-top: 10px;
            }

            .safetyCheck {
                position: relative;
                height: 400px;
                .phone-form {
                    top: 30px;
                    text-align: center;
                    width: 100%;
                    position: relative;
                    .el-tabs {
                        width: 100%;
                        /*margin: 0 auto 60px;*/
                        font-size: 20px;
                        /deep/ .el-tabs__item {
                            font-size: 20px;
                            @extend .no-select;
                        }
                        /deep/ .el-tabs__nav-wrap:after {
                            height: 0;
                        }
                    }
                    .title {
                        margin-top: 20px;
                        font-size: 16px;
                    }
                    .remind {
                        font-size: 12px;
                        color: #999999;
                        margin-top: 10px;
                    }
                }
            }
            .phone-box {
                margin-top: 30px;
                .el-input {
                    width: 100%;
                    /deep/ .el-input-group__append {
                        cursor: pointer;
                        @extend .no-select;
                        border: none !important;
                    }
                }
                .phone-verify {
                    margin-top: 30px !important;
                }

                .el-button {
                    font-size: 14px;
                    display: block;
                    margin: 0 auto;
                    width: 100%;
                }
                .getMess {
                    line-height: 34px;
                    width: 80px;
                    padding: 0;
                    text-align: center;
                }
            }
        }
        /deep/ .el-input{
            width:100%;
            border-bottom: 1px solid #ddd;
            padding-bottom: 10px;
        }
        .phone-submit {
            height: 50px;
            margin-top: 115px !important;
        }
    }
</style>
<style>
    @media screen and (max-width:767px) {
        .el-message-box {
            width: 94% !important;
        }
    }
</style>